import React from 'react'
import './Committees.css'
import Pre from '../../Assets/pre.svg'
import G20 from '../../Assets/g20.png'
import EUN from '../../Assets/EUN.png'
import Aipp from '../../Assets/aipp.jpg'
import Unhrc from '../../Assets/UNHRC.png'
import Undp from '../../Assets/UNDP.png'

const length = 200;
const breadth = 200;
const Committees = () => {
    return (
        <div className="committees p-2">
            <h1>Committees</h1>

            <div className="container-fluid">
                <div className="row">
                    <div className="col-sm-12 col-md-4 py-5">
                        <img src={G20} alt="" className="img-fluid" style={{height: {length}, width:{breadth}}} />
                    </div>
                    <div className="col-sm-12 col-md-4 py-5">
                        <img src={EUN} alt="" className="img-fluid" style={{height: {length}, width:{breadth}}}/>
                    </div>
                    <div className="col-sm-12 col-md-4 py-5">
                        <img src={Aipp} alt="" className="img-fluid" style={{height: {length}, width:{breadth}}}/>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-12 col-md-6 py-5">
                        <img src={Unhrc} alt="" className="img-fluid" style={{height: {length}, width:{breadth}}}/>
                    </div>
                    <div className="col-sm-12 col-md-6 py-5">
                        <img src={Undp} alt="" className="img-fluid" style={{height: {length}, width:{breadth}}}/>
                    </div>
                </div>

            </div>

        </div>
    )
}

export default Committees