import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import './Committee.css';
import G20 from '../../Assets/g20.png'
// import UnscSide from '../../Assets/Unsc-Side.png'


function Home() {
    return (
        <div className="Com-main">
            <section >
                <Container fluid className="home-section" id="home">
                    {/* <Particle /> */}
                    <Container className="home-content">

                        <Row>
                            <div className="com-outer-card">
                                <div className="com-card rounded d-flex justify-content-center align-items-center flex-column">
                                    <img src={G20} alt="" className="com-img" />
                                    {/* <h1 className="p-3">G20</h1> */}
                                </div>
                                {/* <div className="side-img">
                                    <img src={UnscSide} alt="" />
                                </div> */}
                            </div>
                        </Row>

                    </Container>
                </Container>

            </section>

            <div className="committee-content d-flex justify-content-center align-items-center py-5">
                <div className="d-flex justify-content-center align-items-center flex-column rounded pb-5 custom">

                    <Row className="content">
                        The G20 or Group of 20 is an intergovernmental forum comprising 19 sovereign countries, the European Union (EU), and the African Union (AU).
                        The G20 was founded in 1999 in response to several world economic crises.
                        Since 2008, it has convened at least once a year, with summits involving each member's head of government or state, finance minister, or foreign minister, and other high-ranking officials; the EU is represented by the European Commission and the European Central Bank.
                        It works to address major issues related to the global economy, such as international financial stability, climate change mitigation and sustainable development.

                    </Row>

                    <h1 className="pt-4 pb-2">Agenda</h1>

                    <Row className="content">
                        Discussing Prime Minister Narendra Modi's vision for the expansion of the United Nations Security Council.
                    </Row>

                </div>
            </div>
        </div>
    );
}

export default Home;
