import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import './Committee.css';
import Unhrc from '../../Assets/UNHRC.png'
// import UnscSide from '../../Assets/Unsc-Side.png'


function Home() {
    return (
        <div className="Com-main">
            <section >
                <Container fluid className="home-section" id="home">
                    {/* <Particle /> */}
                    <Container className="home-content">

                        <Row>
                            <div className="com-outer-card">
                                <div className="com-card rounded d-flex justify-content-center align-items-center flex-column">
                                    <img src={Unhrc} alt="" className="com-img" />
                                    <h1 className="p-3">UNHRC</h1>
                                </div>
                                {/* <div className="side-img">
                                    <img src={UnscSide} alt="" />
                                </div> */}
                            </div>
                        </Row>

                    </Container>
                </Container>

            </section>

            <div className="committee-content d-flex justify-content-center align-items-center py-5">
                <div className="d-flex justify-content-center align-items-center flex-column rounded pb-5 custom">

                    <Row className="content">
                        The United Nations Human Rights Council (UNHRC)[a] is a United Nations body whose mission is to promote and protect human rights around the world. The Council has 47 members elected for staggered three-year terms on a regional group basis. The headquarters of the Council are at the United Nations Office at Geneva in Switzerland. The Council investigates allegations of breaches of human rights in United Nations member states and addresses thematic human rights issues like freedom of association and assembly, freedom of expression, freedom of belief and religion, women's rights, LGBT rights, and the rights of racial and ethnic minorities.


                    </Row>

                    <h1 className="pt-4 pb-2">Agenda</h1>

                    <Row className="content">
                        Marginalization of the LGBTQIA+ community.
                    </Row>

                </div>
            </div>
        </div>
    );
}

export default Home;
