import React, { useState, useEffect } from "react";
import Preloader from "../src/components/Pre";
import Navbar from "./components/Navbar";
import Home from "./components/Home/Home";
import G20 from "./components/Committee/G20";
import Aippm from "./components/Committee/Aippm";
import Undp from "./components/Committee/Undp";
import Unhrc from "./components/Committee/Unhrc";
import Unsc from "./components/Committee/Unsc";
import Footer from "./components/Footer";
import {
  BrowserRouter as Router,
  Route,
  Routes,
  Navigate
} from "react-router-dom";
import ScrollToTop from "./components/ScrollToTop";
import "./style.css";
import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";

function App() {
  const [load, upadateLoad] = useState(true);

  useEffect(() => {
    const timer = setTimeout(() => {
      upadateLoad(false);
    }, 1200);

    return () => clearTimeout(timer);
  }, []);

  return (
    <Router>
      <Preloader load={load} />
      <div className="App" id={load ? "no-scroll" : "scroll"}>
        <Navbar />
        <ScrollToTop />
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/g20" element={<G20 />} />
          <Route path="/aippm" element={<Aippm />} />
          <Route path="/unsc" element={<Unsc />} />
          <Route path="/unhrc" element={<Unhrc />} />
          <Route path="/undp" element={<Undp />} />
          <Route path="*" element={<Navigate to="/"/>} />
        </Routes>
        <Footer />
      </div>
    </Router>
  );
}

export default App;
